import React from "react";
import "./CategoryBar.sass"

interface CategoryBarProps {
  title: string;
}

export class CategoryBar extends React.Component<CategoryBarProps> {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        {/* <h1>
          {this.props.title}
        </h1> */}
        <div className="category-bar">
          {this.props.title}
        </div>
      </div>
    );
  }
};

